import React from 'react';
import { UseQuerygql } from '../hooks/useQuerygql';



export default function ProductTest() {

    const { error, data, loading } = UseQuerygql();

    

    if (loading) return <span className="loader"></span>
if (error) return <div className="error">There is an error</div>

    return <div className='one'><span><a href="search"><button>Search User</button></a></span>
        <h1>Hi, My name is {data.user.name}</h1>
                    <img src={data.user.avatarUrl} alt={data.user.name} />
                        <h3>{data.user.bio}</h3>
                        <h3>My Location is {data.user.location}</h3>
    </div>;
}