import { useQuery, gql } from '@apollo/client';


const GET_PRODUCTS = gql `
query { 
  user(login: "nzhannu" ) { 
       avatarUrl
      bio
    name
    location
      }
  }
`;
export const UseQuerygql = () => {
    const { error, data, loading } = useQuery(GET_PRODUCTS);

    return {
        error, data, loading,
    };
};
