import './App.css';
import ProductTest from './pages/ProductTest';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Search from './pages/Search';


function App() {
  return (
    <div className="App">
      <BrowserRouter>
     <Switch>
     <Route strict exact path="/" component={ProductTest} />
     <Route strict exact path="/search" component={Search} />
     </Switch>
     </BrowserRouter>
    </div>
  );
}


export default App;