import React, {useState } from 'react';
import {gql, useLazyQuery } from "@apollo/client";

const GET_INFO = gql `
query getInfo($name: String!) { 
  user(login: $name) { 
       avatarUrl
      bio
    name
    location
      }
  }
`;


export  default function Search()
 {
     const [name, setName ] = useState ("");

     const [getInfo, { loading, error, data }] = useLazyQuery(GET_INFO, {
        variables: {
            name
        }
     });

     return (
        <div className='searchClass'><a href="/"><button className='home'>Home page</button></a>
        <h1>Search Github Users by Username</h1>
            <input placeholder="type username here" value={name} onChange={(e) => setName(e.target.value)} />
                <button onClick={() => getInfo()}>Search</button>
                {loading && <span className="loader"></span>}
                {error && <div className="error">User Not Found with this username "{name}"</div>}
                {data && (
                    <div className='one'>
                    <h1>Hi, My name is {data.user.name}</h1>
                    <img src={data.user.avatarUrl} alt={data.user.name} />
                        <h3>{data.user.bio}</h3>
                        <h3>My Location is {data.user.location}</h3>
                </div>
                )}
        </div>
    );
}